import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import PropTypes from 'prop-types';

// Import Style
import { TextComp } from './styled';
const children = this.props.children;
const Text = ( props ) => (
  	<TextComp {...props}>
  		{children}
  	</TextComp>
);
Text.propTypes = {
  children: PropTypes.string
};
export default(Text);
